<template>
  <div class="container">
    <div id="amap-container">
    </div>
    <div class="citySelect-container">
      <el-row>
        <el-select v-model="province" v-on:change="search(province, 'province')" placeholder="请选择省">
          <el-option
            v-for="item in provinceList"
            :key="item.adcode"
            :label="item.name"
            :value="item.adcode">
          </el-option>
        </el-select>
      </el-row>
      <el-row>
        <el-select v-model="city" v-on:change="search(city, 'city')" placeholder="请选择市">
          <el-option
            v-for="item in cityList"
            :key="item.adcode"
            :label="item.name"
            :value="item.adcode">
          </el-option>
        </el-select>
      </el-row>
      <el-row>
        <el-select v-model="district" v-on:change="search(district, 'district')" placeholder="请选择区">
          <el-option
            v-for="item in districtList"
            :key="item.adcode"
            :label="item.name"
            :value="item.adcode">
          </el-option>
        </el-select>
      </el-row>
      <el-row>
        <el-select v-model="street" v-on:change="search(street, 'street')" placeholder="请选择街道">
          <el-option
            v-for="item in streetList"
            :key="item.adcode"
            :label="item.name"
            :value="item.adcode">
          </el-option>
        </el-select>
      </el-row>
      <el-row type="flex" justify="space-between" style="text-align: right;">
        <el-col :span="12">
          <el-checkbox v-model="toWGS" label="转WGS" border></el-checkbox>
        </el-col>
        <el-col :span="12">
          <el-button type="primary" icon="el-icon-download" v-on:click="downloadGeoJson">下 载</el-button>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import AMap from 'AMap'
import FileSaver from 'file-saver'
import CoordinateConvert from 'coordinate-convert'
var map, districtSearch
export default {
  name: 'aMap',
  data () {
    return {
      geoBounds: [],
      bounds: [],
      province: '',
      city: '',
      district: '',
      street: '',
      provinceList: [],
      cityList: [],
      districtList: [],
      streetList: [],
      toWGS: true // 转换为WGS
    }
  },
  methods: {
    // 初始化
    init: function () {
      map = new AMap.Map('amap-container', {
        zoom: 14,
        mapStyle: 'amap://styles/whitesmoke'
      })
      map.setCenter([116.397428, 39.90923])
      // 初始化行政区划搜索
      AMap.service('AMap.DistrictSearch', function () {
        // 在对象初始化的时候设定
        districtSearch = new AMap.DistrictSearch({
          subdistrict: 1, // 返回下一级行政区
          showbiz: false // 最后一级返回街道信息
        })
      })
      // 初始化省列表
      this.searchDistrict('中国')
    },
    // 获取边界和下级城市数据
    getData (data, level, isAll) {
      let _ = this
      if (level) {
        // 清空下一级的下拉列表
        _.clearSelectList(level)
      }
      // 边界
      if (data.boundaries) {
        _.geoBounds.push({
          code: data.adcode,
          name: data.name,
          boundaries: data.boundaries
        })
        var len = data.boundaries.length
        for (let i = 0; i < len; i++) {
          var polygon = new AMap.Polygon({
            strokeWeight: 1,
            strokeColor: '#CC66CC',
            fillColor: '#CCF3FF',
            fillOpacity: 0.5,
            path: data.boundaries[i]
          })
          polygon.setMap(map)
        }
        map.setFitView() // 地图自适应
      }
      // 下级城市
      if (!isAll) {
        _.addSubList(data.adcode, data.districtList)
      }
    },
    // 清空列表
    clearSelectList: function (level) {
      let _ = this
      if (level === 'province') {
        _.cityList = []
        _.city = ''
        _.districtList = []
        _.district = ''
        _.streetList = []
        _.street = ''
      } else if (level === 'city') {
        _.districtList = []
        _.district = ''
        _.streetList = []
        _.street = ''
      } else if (level === 'district') {
        _.streetList = []
        _.street = ''
      }
    },
    // 添加下级城市
    addSubList: function (adcode, subList) {
      let _ = this
      var l = subList[0].level
      var all = {adcode: adcode, name: '全部'}
      if (l === 'province') {
        _.provinceList = subList
        _.provinceList.unshift(all)
      } else if (l === 'city') {
        _.cityList = subList
        _.cityList.unshift(all)
      } else if (l === 'district') {
        _.districtList = subList
        _.districtList.unshift(all)
      } else if (l === 'street') {
        _.streetList = subList
        _.streetList.unshift(all)
      }
    },
    // 根据adcode搜索(change事件)
    search: function (adcode, level) {
      let _ = this
      // 清除地图
      map.clearMap()
      _.geoBounds = []
      districtSearch.setLevel(level) // 行政区级别
      districtSearch.setExtensions('all')
      if (level === 'province' && adcode.endsWith('100000')) {
        // 全国
        for (let i = 1; i < _.provinceList.length; i++) {
          const code = _.provinceList[i].adcode
          _.searchDistrict(code, level, true)
        }
      } else if (level === 'city' && adcode.endsWith('0000')) {
        // 全部市
        for (let i = 1; i < _.cityList.length; i++) {
          const code = _.cityList[i].adcode
          _.searchDistrict(code, level, true)
        }
      } else if (level === 'district' && adcode.endsWith('00')) {
        // 全部区
        for (let i = 1; i < _.districtList.length; i++) {
          const code = _.districtList[i].adcode
          _.searchDistrict(code, level, true)
        }
      } else {
        _.searchDistrict(adcode, level, false)
      }
    },
    // 行政区查询
    // 按照adcode进行查询可以保证数据返回的唯一性
    searchDistrict: function (adcode, level, isAll) {
      let _ = this
      const loading = _.$loading({ })
      districtSearch.search(adcode, function (status, result) {
        loading.close()
        if (status === 'complete') {
          _.getData(result.districtList[0], level, isAll)
        }
      })
    },
    downloadGeoJson: function () {
      let _ = this
      var b = _.geoBounds // 边界坐标
      if (b.length === 0) {
        _.$message({
          type: 'error',
          center: true,
          message: '当前未选择城市，无法下载'
        })
        return
      }
      var res = _.getGeoJson(b)
      // 保存文件
      var blob = new Blob([JSON.stringify(res)], {
        type: 'text/plain;charset=utf-8'
      })
      FileSaver.saveAs(blob, 'coods.json')
    },
    getGeoJson: function (data) {
      // 边界GeoJson
      var result = {
        'type': 'FeatureCollection',
        'features': []
      }
      for (let i = 0; i < data.length; i++) {
        const p = data[i]
        var coods = [] // 坐标
        var feature = {
          'type': 'Feature',
          'properties': {code: p.code, name: p.name},
          'geometry': {
            'type': 'Polygon',
            'coordinates': [coods]
          }
        }
        var b = p.boundaries
        if (b.length > 1) {
          // 多面
          feature.geometry.type = 'MultiPolygon'
          for (let i = 0; i < b.length; i++) {
            coods[i] = []
            for (let j = 0; j < b[i].length; j++) {
              const cood = b[i][j]
              coods[i].push(this.toWGS ? CoordinateConvert.gcj2wgs(cood.lng, cood.lat) : [cood.lng, cood.lat])
            }
          }
        } else {
          // 单面
          for (let i = 0; i < b[0].length; i++) {
            const cood = b[0][i]
            coods.push(this.toWGS ? CoordinateConvert.gcj2wgs(cood.lng, cood.lat) : [cood.lng, cood.lat])
          }
        }
        result.features.push(feature)
      }
      return result
    }
  },
  mounted: function () {
    this.init() // 初始化
  }
}
</script>

<style scoped>
  .container, #amap-container {
    width: 100%;
    height: 100%;
  }
  .citySelect-container {
    position: fixed;
    top: 10px;
    left: 10px;
    z-index: 9;
  }
  .el-row {
    margin-bottom: 8px;
    &:last-child {
      margin-bottom: 0;
    }
  }
</style>



